@import "src/style/global.scss";
@import "src/style/screen-size.scss";

.menu {
  position: fixed;
  top: 75px;
  width: 366px;
  padding: 20px 12px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  background-color: var(--background-color-secondary);
  box-shadow: $widget-shadow;
  border-radius: 24px;

  @include respond-to(1390px) {
    top: 75px;
    width: 340px;
    gap: 14px;
  }

  @include respond-to(1370px) {
    top: 65px;
    width: 340px;
    gap: 12px;
  }

  @include respond-to(1260px) {
    width: 320px;
  }

  @include respond-to(lg) {
    top: 55px;
    width: 300px;
    gap: 10px;
    padding: 10px 10px 16px 10px;
  }

  @include respond-to(md) {
    top: 50px;
    right: 20px;
    width: 260px;
    gap: 10px;
  }

  @include respond-to(710px) {
    position: absolute;
    top: 50px;
    right: 50%;
    transform: translate(50%, 0);
    width: 90%;
  }

  .person_title {
    gap: 30px;
    display: none;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-bottom: 1px solid $border-color;
    padding-bottom: 5px;

    @include respond-to(710px) {
      display: flex;
    }

    @include respond-to(xs) {
      gap: 20px;
    }

    @include respond-to(xxs) {
      gap: 10px;
    }

    @include respond-to(311px) {
      gap: 8px;
    }

    #person_title_img {
      width: 40px;
      height: 40px;
      display: block !important;

      @include respond-to(xs) {
        width: 38px;
        height: 38px;
      }

      @include respond-to(xxs) {
        width: 34px;
        height: 34px;
      }

      @include respond-to(311px) {
        width: 30px;
        height: 30px;
      }
    }

    p {
      font-family: $mts-wide-m;
      font-weight: 500;
      color: var(--text-color);
      font-size: 18px;

      @include respond-to(xs) {
        font-size: 17px;
      }

      @include respond-to(xxs) {
        font-size: 16px;
      }

      @include respond-to(311px) {
        font-size: 14px;
      }
    }
  }
}
