@import "../../style/global.scss";

.box {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  margin-top: 50px;

  .table_head {
    display: flex;
    width: 90%;
    align-items: center;
    justify-content: space-between;
    height: 41px;
    border-radius: 10px;
    background: var(--background-table-head);
    margin-bottom: 20px;
    color: $color-white;
    box-shadow: 0 4px 8px 1px rgba(0, 0, 0, 0.2);

    .table_cell_wrapper {
      border-right: 2px solid #fff;
      width: 100%;
      &:last-child {
        border-right: none;
      }

      .table_cell {
        border-bottom: none;
        color: $color-white;
        font-family: $mts-wide-b;
        font-size: 16px;

        padding: 0 20px;
        line-height: 41px;
      }
    }

    .check {
      border-right: 2px solid #fff;
      border-bottom: none;
      .checkbox path {
        color: #fff;
      }
    }
  }

  .table_container {
    overflow: hidden;
    background: var(--background-table-container);
    .table_row {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 41px;
      cursor: pointer;


      .check_row {
        //background: var(--background-table-container);
        border-bottom: var(--border-table-rows);
        .checkbox_row {
          path {
            color: var(--text-color );
          }
        }
      }

      .table_cell_row {
        display: block;
        width: 100%;
        padding: 0 20px;
        line-height: 41px;
        font-family: $mts-text-r;
        font-size: 16px;
        color: var(--text-color);
        border-bottom: var(--border-table-rows);
      }
    }
  }
}
