@import "src/style/global.scss";
@import "src/style/screen-size.scss";

.multiTool {
  padding: 8px;
  border-radius: 8px;
  background-color: var(--background-color-secondary);
  position: fixed;
  bottom: 20vh;
  right: 5vw;
  cursor: pointer;
  box-shadow: var(--multiTool-shadow);
  display: none;
  align-items: center;
  justify-content: center;

  @include respond-to(710px) {
    display: flex;
  }

  @include respond-to(xxs) {
    padding: 6px;
  }

  .multiToolIcon,
  .crossIcon {
    width: 34px;
    height: 34px;

    path {
      fill: var(--fill-icons);
    }

    @include respond-to(xxs) {
      width: 32px;
      height: 32px;
    }

    @include respond-to(280px) {
      width: 28px;
      height: 28px;
    }
  }

  .multiToolIcon {
    @include respond-to(xxs) {
      width: 32px !important;
      height: 32px !important;
    }

    @include respond-to(280px) {
      width: 30px !important;
      height: 30px !important;
    }
  }

  &__buttons {
    position: absolute;
    top: -175px;
    display: flex;
    flex-direction: column;
    gap: 10px;

    @include respond-to(xxs) {
      top: -163px;
    }

    @include respond-to(280px) {
      top: -150px;
    }

    &__btn {
      padding: 10px;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: var(--background-color-secondary);
      box-shadow: var(--multiTool-shadow);
      align-items: center;
      justify-content: center;
      cursor: pointer;

      path {
        fill: var(--fill-icons);
      }

      @include respond-to(xxs) {
        width: 44px;
        height: 44px;
      }

      @include respond-to(280px) {
        width: 40px;
        height: 40px;
      }
    }
  }
}
