@import "src/style/global.scss";
@import "src/style/screen-size.scss";

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 0.1 ease;

  &--after-open {
    opacity: 1;
  }

  &--before-close {
    opacity: 0;
  }
}

.ProfileModal__Overlay {
  position: fixed;
  background-color: rgba($color-black, 0.5);
  inset: 0;
  padding: 32px;
  z-index: 100;
}

.ProfileModal__Content__Container {
  min-height: fit-content;
  max-height: 85vh;
  overflow-y: auto;
  overflow-x: hidden;

  @include respond-to(xs) {
    max-height: 94vh;
  }

  &::-webkit-scrollbar {
    width: 8px;
    height: 5px;
  }

  &::-webkit-scrollbar-track {
    background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $border-color;
    border-radius: 5px;
  }
}

.ProfileModal__Content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 700px;
  width: 100%;
  border-radius: 24px;
  background-color: var(--background-color);
  overflow-x: hidden;
  overflow-y: hidden;
  padding: 10px 8px;

  &:focus-visible {
    outline: none;
  }

  @include respond-to(md) {
    border-radius: 18px;
  }

  @include respond-to(sm) {
    top: 20%;
    left: 50%;
    transform: translate(-50%, -20%);
    width: 95%;
  }

  &__Close {
    position: absolute;
    top: 12px;
    right: 20px;
    width: 24px;
    height: 24px;
    cursor: pointer;

    path {
      fill: var(--fill-icons);
    }
  }

  &__Title {
    text-align: left;
    padding: 24px 32px;
    font-family: $mts-wide-b;
    font-size: 24px;
    border-bottom: 1px solid $border-color;

    @include respond-to(1390px) {
      font-size: 22px;
    }

    @include respond-to(lg) {
      font-size: 18px;
    }

    @include respond-to(sm) {
      font-size: 16px;
      padding: 16px;
    }
  }

  &__Inner {
    width: 100%;

    &__Text {
      font-family: $mts-wide-m;
      font-size: 22px;
      margin-bottom: 10px;

      @include respond-to(1390px) {
        font-size: 20px;
      }

      @include respond-to(lg) {
        font-size: 16px;
      }

      @include respond-to(sm) {
        font-size: 14px;
      }
    }
  }
}
.ProfileModal {
  &__person_title {
    gap: 30px;
    display: none;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-bottom: 1px solid $border-color;
    padding: 12px 0;

    @include respond-to(710px) {
      display: flex;
    }

    @include respond-to(xs) {
      gap: 20px;
    }

    @include respond-to(xxs) {
      gap: 10px;
    }

    @include respond-to(311px) {
      gap: 8px;
    }

    #person_title_img {
      width: 40px;
      height: 40px;
      display: block !important;

      @include respond-to(xs) {
        width: 38px;
        height: 38px;
      }

      @include respond-to(xxs) {
        width: 34px;
        height: 34px;
      }

      @include respond-to(311px) {
        width: 30px;
        height: 30px;
      }
    }

    p {
      font-family: $mts-wide-m;
      font-weight: 500;
      color: var(--text-color);
      font-size: 18px;

      @include respond-to(xs) {
        font-size: 17px;
      }

      @include respond-to(xxs) {
        font-size: 16px;
      }

      @include respond-to(311px) {
        font-size: 14px;
      }
    }
  }

  &__info {
    display: flex;
    font-family: $mts-wide-r;
    font-size: 22px;
    font-weight: 100;
    width: 100%;
    border-top: 1px solid $border-color;
    text-align: left;
    padding: 12px;

    @include respond-to(xs) {
      font-size: 16px;
    }

    @include respond-to(xxs) {
      font-size: 14px;
    }

    @include respond-to(311px) {
      font-size: 12px;
    }
  }

  &__person_info {
    width: 100%;
    padding: 10px;
    border-top: 1px solid $border-color;
    justify-content: space-between;
    gap: 10px 0;
    display: grid;
    grid-template-columns: 1fr 1fr;

    @include respond-to(xs) {
      padding: 10px;
    }

    @include respond-to(xxs) {
      padding: 10px;
    }

    @include respond-to(311px) {
      padding: 10px;
    }

    div {
      font-family: $mts-wide-r;
      font-size: 20px;

      @include respond-to(xs) {
        font-size: 16px;
      }

      @include respond-to(xxs) {
        font-size: 14px;
      }

      @include respond-to(311px) {
        font-size: 12px;
      }

      p {
        font-family: $mts-text-r;
        font-size: 16px;
        color: #a6a6a6;

        @include respond-to(xs) {
          font-size: 16px;
        }

        @include respond-to(xxs) {
          font-size: 14px;
        }

        @include respond-to(311px) {
          font-size: 12px;
        }
      }
    }
  }
}
